@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  :root {
    --background: 0 0% 100%;
    --foreground: 222.2 47.4% 11.2%;

    --muted: 210 40% 96.1%;
    --muted-foreground: 215.4 16.3% 46.9%;

    --popover: 0 0% 100%;
    --popover-foreground: 222.2 47.4% 11.2%;

    --border: 214.3 31.8% 91.4%;
    --input: 214.3 31.8% 91.4%;

    --card: 0 0% 100%;
    --card-foreground: 222.2 47.4% 11.2%;

    --primary: 222.2 47.4% 11.2%;
    --primary-foreground: 210 40% 98%;

    --secondary: 210 40% 96.1%;
    --secondary-foreground: 222.2 47.4% 11.2%;

    --accent: 210 40% 96.1%;
    --accent-foreground: 222.2 47.4% 11.2%;

    --destructive: 0 100% 50%;
    --destructive-foreground: 210 40% 98%;

    --ring: 215 20.2% 65.1%;

    --radius: 0.5rem;
  }

  .dark {
    --background: 224 71% 4%;
    --foreground: 213 31% 91%;

    --muted: 223 47% 11%;
    --muted-foreground: 215.4 16.3% 56.9%;

    --accent: 216 34% 17%;
    --accent-foreground: 210 40% 98%;

    --popover: 224 71% 4%;
    --popover-foreground: 215 20.2% 65.1%;

    --border: 216 34% 17%;
    --input: 216 34% 17%;

    --card: 224 71% 4%;
    --card-foreground: 213 31% 91%;

    --primary: 210 40% 98%;
    --primary-foreground: 222.2 47.4% 1.2%;

    --secondary: 222.2 47.4% 11.2%;
    --secondary-foreground: 210 40% 98%;

    --destructive: 0 63% 31%;
    --destructive-foreground: 210 40% 98%;

    --ring: 216 34% 17%;

    --radius: 0.5rem;
  }
}

@font-face {
  font-family: 'SF Pro';
  src: url('../public/fonts/SFPro-Regular.eot');
  src: url('../public/fonts/SFPro-Regular.eot?#iefix') format('embedded-opentype'),
      url('../public/fonts/SFPro-Regular.woff2') format('woff2'),
      url('../public/fonts/SFPro-Regular.woff') format('woff'),
      url('../public/fonts/SFPro-Regular.ttf') format('truetype'),
      url('../public/fonts/SFPro-Regular.svg#SFPro-Regular') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@layer base {
  * {
    @apply border-border;
  }

  /* body {
    background: none !important;
  } */

  body {
    @apply dark bg-background text-foreground;
    font-feature-settings: "rlig" 1, "calt" 1;
    font-family: 'SF Pro' !important;
    background: #0F0F0F;
  }

  .mx-auto.mt-4.h-2.w-\[100px\].rounded-full.bg-gray-100.dark\:bg-gray-800 {
    display: none;
  }

  .no-white:focus-visible {
    outline: none;
  }

  .im-upload .p-6.pt-0 {
    padding: 0;
  }

  .black_close {
    color: #000 !important;
  }

}